body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-montserrat: Montserrat;

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-8xl: 27px;
  --font-size-21xl: 40px;
  --font-size-5xl: 24px;
  --font-size-56xl: 75px;
  --font-size-11xl: 30px;
  --font-size-18xl: 37px;
  --font-size-6xl: 25px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-darkslateblue-100: #214f94;
  --color-darkslateblue-200: rgba(33, 79, 148, 0.08);
  --color-steelblue: #6b87b0;
  --color-lavender: #cdddf2;
  --color-aliceblue-100: #edf1f6;
  --color-cornflowerblue: #3d71bd;
  --color-slategray: rgba(86, 104, 132, 0.84);
  --color-gainsboro: #d9d9d9;

  /* Gaps */
  --gap-10xs: 3px;
  --gap-18xl: 37px;
  --gap-14xl: 33px;
  --gap-6xl: 25px;
  --gap-7xs: 6px;
  --gap-16xl: 35px;
  --gap-19xl: 38px;
  --gap-lgi: 19px;
  --gap-4xs: 9px;
  --gap-5xs: 8px;

  /* Paddings */
  --padding-9xl: 28px;
  --padding-5xl: 24px;
  --padding-12xl: 31px;

  /* border radiuses */
  --br-xl: 20px;
  --br-781xl: 800px;
  --br-8xs: 5px;
  --br-sm: 14px;
}
